/**
 * 获取振动系测点的类型
 * flag：默认为当前测点的类型分类标记flag_pos
 * flag：1:振动、振动阶次 2：包络、包络阶次 3：晃度 4：倾覆 5：倾角 6：合成倾角 7：沉降 8：螺栓 9：冲击,冲击阶次 10:轨道波磨
 * type表示测点类型
 *
 */
export function getFlagType(type){
    var flag;
    switch (parseInt(type)) {
        case 3: case 8:
            flag=1;
            break;
        case 4:
            flag=2;
            break;
        case 9:
            flag=2;
            break;
        case 6:
            flag=3;
            break;
        case 11:
            flag=4;
            break;
        case 12:
            flag=5;
            break;
        case 13:
            flag=6;
            break;
        case 14:
            flag=8;
            break;
        case 15: case 16:
            flag=9;
            break;
        case 17:
            flag=10;
            break;
    }
    return flag;
}
