/**
 * 各测点共同的特征值
 */
export function isCommonCode(code){
    if(code>12000 && code<14000){
        return true;
    }
    return false;
}
/**
 * *报警门限转换
 * **/
export function alarmIndex(val){
    let posTypeVal="";
    switch (parseInt(val)) {
        case 0:
            posTypeVal = "1";
            break;
        case 1:
            posTypeVal = "1.4";
            break;
        case 2:
            posTypeVal = "2.3";
            break;
        case 3:
            posTypeVal = "2.8";
            break;
        case 4:
            posTypeVal = "3";
            break;
        case 5:
            posTypeVal = "3.5";
            break;
        case 6:
            posTypeVal = "4";
            break;
        case 7:
            posTypeVal = "4.5";
            break;
        case 8:
            posTypeVal = "5";
            break;
        case 9:
            posTypeVal = "7.1";
            break;
        case 10:
            posTypeVal = "11";
            break;
        case 11:
            posTypeVal = "20";
            break;
        case 12:
            posTypeVal = "30";
            break;
        case 13:
            posTypeVal = "40";
            break;
        case 14:
            posTypeVal = "50";
            break;
    }
    return posTypeVal;
}
// 数组重新排序
export function arraymove(arr, fromIndex, toIndex) {
    var element = arr[fromIndex];
    arr.splice(fromIndex, 1);
    arr.splice(toIndex, 0, element);
}
//
/**
 * *遍历数据
 *
 * **/
export function getDataList(src) {
    let arr = src[0]
    for(let i=1; i<src.length; i++){
        arr = gridArr(arr, src[i])
    }
    return arr;
}
export function gridArr(arr1, arr2) {
    const arr = []
    for(let i=0; i<arr1.length; i++){
        for(let j =0; j<arr2.length; j++){
            arr.push(arr1[i] + '_' + arr2[j])
        }
    }
    return arr
}
